@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
body{font-family:'Barlow', sans-serif;display:flex;justify-content:center;align-items:center;height:100vh;background:linear-gradient(to left, #4b79a1, #283e51);background-size:cover;background-repeat:no-repeat}

.person{margin:10px auto;padding:20px;min-width:300px;min-height:140px;max-width:500px;border-radius:5px;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;background:linear-gradient(to left, #457fca, #5691c8);color:white;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.person:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.person__age{color:#ccc}.person__partner{color:#fff}

